#stakessp {
  position: relative;
  width: 100%;
  max-width: 750px;
  margin: 0 auto 20px;
  border: none;
  font-weight: normal;
  font-size: 14px;
  align-items: center;
  text-align: center;
  letter-spacing: -0.3px;
  text-align: left;
  background: #fff;
  border-radius: 15px;

  .header {
    background: #3782fa;
    border-radius: 2px;
    // height: 46px;
    padding-left: 18px;
    color: #ffffff;
    font-size: 14px;
    width: 100%;
    line-height: 46px;
    margin-bottom: 18px;
  }

  .tokens {
    width: 100%;
    // background: #fff;
    color: #333;
    padding: 20px 28px 15px;
    border-radius: 15px;
    // box-shadow: 0px 20px 40px rgb(148 148 148 / 6%);
    margin-bottom: 18px;

    // &.english {
    //   padding: 20px 25px 15px;
    // }

    legend {
      margin: 20px 0 16px;
      color: #333;
      display: block;
    }

    p {
      font-size: 13px;
      line-height: 24px;
      display: flex;
      align-items: center;
      text-align: center;
      letter-spacing: -0.3px;
      color: #333;
    }

    span {
      margin: 0 5px;
    }

    button {
      margin: 10px auto;
      padding: 0 5px;
    }
  }

  .about {
    width: 100%;
    background: #fff;
    padding-top: 10px;
    color: #333;
    margin-top: 18px;
    padding: 28px 20px 15px;
    border-radius: 15px;
    // box-shadow: 0px 20px 40px rgb(148 148 148 / 6%);
    margin-bottom: 10px;

    legend {
      margin-top: 20px;
      color: #333;
      display: block;
    }

    li {
      margin: 0 20px;
      list-style: disc !important;
      line-height: 24px;
      letter-spacing: 0.5px;
      word-break: break-word;
    }
  }

  .table {
    width: 100%;
    // background: #fff;
    padding: 20px 35px 22px;
    border-radius: 15px;
    // box-shadow: 0px 20px 40px rgb(148 148 148 / 6%);
    // padding: 0 5px 5px;
    border-collapse: collapse;
    width: 100%;
    color: #333;
    margin-bottom: 0;
    legend {
      line-height: 44px;
    }
  }

  table {
    width: 100%;

    tr {
      border-top: 1px solid #e5e5e5;
      border-bottom: 1px solid #e5e5e5;
    }

    td,
    th {
      line-height: 32px;
      border-right: 1px solid #e5e5e5;
      border-left: 1px solid #e5e5e5;
      padding-left: 3px;
      font-size: 13px;
    }

    span {
      margin-left: 3px;
    }
  }

  .wrap {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: flex-start;
  }

  .column {
    width: calc(50% - 36px);
    background: #fff;
    padding: 20px 20px 32px;
    border-radius: 10px;
    border: 1px solid #efefef;
    color: #333;
    margin-bottom: 18px;
    height: 385px;

    &.english {
      width: calc(50% - 30px);
      padding: 20px 5px 32px;
    }

    &.ssp {
      height: 310px;
    }

    fieldset {
      padding: 10px;
    }

    legend {
      margin-top: 20px;
      color: #333;
      display: block;
    }

    p {
      font-size: 14px;
      line-height: 24px;
      display: flex;
      align-items: center;
      text-align: center;
      letter-spacing: -0.3px;
      color: #333;
      margin-bottom: 3px;
    }

    input {
      width: 100%;
      height: 28px;
      line-height: 20px;
      padding: 3px;
      border-radius: 0;
      background: #fff;
      color: #333;
      margin-bottom: 6px;
      font-size: 14px;
      line-height: 22px;
      padding: 4px 11px;
      margin-bottom: 10px;
    }

    .apy {
      span {
        margin: 0 3px;
      }
    }
  }

  .st-mix {
    display: flex;
    justify-content: space-between;
    margin: 5px 0;
    padding-bottom: 15px;
    border-bottom: 1px dashed #e5e5e5;
    font-size: 13px;
    color: #999;
    // height: 42px;
    line-height: 32px;

    .btn {
      width: 180px;
      height: 32px;
      line-height: 32px;
      border-radius: 5px;
      text-align: center;
      color: #fff;
      font-size: 14px;
      padding: 0;
      margin: 0 auto;
    }
  }

  hr {
    background: #e5e5e5;
    border: 0;
    height: 1px;
  }

  .claimBtn {
    display: block;
    width: 90%;
    margin: 5px auto;
  }
}

@media screen and (max-width: 800px) {
  #stakessp {
    width: calc(100% - 30px);
    .about {
      padding: 20px 15px 15px;
    }
    .column,
    .column.english {
      width: 100%;
      padding: 20px 0 15px;
      margin: 0 15px 15px;
      // padding: 20px 15px 15px;
    }
    .tokens {
      padding: 20px 25px 15px;
      button {
        margin: 5px auto 5px;
      }
    }
  }
}

@media screen and (max-width: 500px) {
  #stakessp {
    // margin-top: 70px;
    p.mobile {
      display: block;
      text-align: left;
      span {
        display: block;
        margin: 0;
      }
    }
    .tokens {
      margin-bottom: 0;
    }
    .table {
      padding: 0px 25px 22px;
    }
  }
}
